.main-footer {
    background-color: #002529;
    height: 196px;
    margin-top: 73px;
    display: grid;
    grid-template-columns:
        minmax(2rem, 1fr)
        minmax(auto, $normal-breakpoint)
        minmax(2rem, 1fr);

    > * {
        grid-column: 2;
    }

    > div {
        display: flex;
        gap: 125px;


        > div {
            align-self: flex-start;
            padding-top: 50px;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: flex-start;
            grid-column: 2;
            gap: 30px;
        
            .main-logo {
                height: 40px;
            }

            nav {
                display: flex;
                align-items: center;
                gap: 20px;
            }
        }

        p {

            margin: 0 0 ;
            padding-top: 50px;
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            font-size: 15px;
            font-weight: 500;
            line-height: 25px;
            color: #ffffff96;
        }
    }
}
