.main-header {
    margin-top: 73px;
    display: grid;
    grid-template-columns:
        minmax(2rem, 1fr)
        minmax(auto, $normal-breakpoint)
        minmax(2rem, 1fr);

    > * {
        grid-column: 2;
    }

    > div {
        display: flex;
        align-items: center;
        justify-content: flex-start;
        grid-column: 2;
        gap: 80px;
        width: 100%;

        .main-logo {
            height: 40px;
      
        }

        nav{
            display: flex;
            align-items: center;
            gap: 45px;
            
            a{
                font-weight: 500;
                font-size: 18px;
                line-height: 28px;
            }
        }

        .header-btn{
            flex:1;
            justify-self: flex-end;
            display: flex;
            align-items: center;
            justify-content: flex-end;

            
            button{
                font-family: inherit;
                font-size: 18px;
                font-weight: 500;
                line-height: 28px;
                color: white;
                background-color: transparent;
                border: 2px solid white;
                width: 153px;
                padding: 10px 30px;
                border-radius: 50px;


            }
        }
    }
}
