.home-page {
    .main-hero {
        .bg-element1 {
            position: absolute;
            left:0;
            transform: translateX(-50%);
        }
        .bg-element2 {
            position: absolute;
            right:0;
            top: 593px;
            transform: translateX(-50%);
        }

        margin-top: 129px;
        padding-bottom: 310px;
        display: flex;
        height: 200px;
        justify-content: space-between;

        h1 {
            font-family: livvic, sans-serif;
            font-weight: bold;
            font-size: 100px;
            color: white;
            width: 50%;

            span {
                color: #f67e7e;
            }
        }

        .hero-txt {
            width: 40%;
            height: 100%;
            align-self: flex-end;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            align-items: flex-start;

            .hero-rect {
                height: 4px;
                width: 50px;
                background-color: #79c8c7;
            }

            p {
                font-family: livvic, sans-serif;
                font-weight: 500;
                font-size: 18px;
                color: white;
            }
        }
    }

    .second-hero {
        .background-div {
            position: absolute;
            left: 0;
            width: 100%;
            height: 684px;
            background-color: #012f34;
            z-index: -2;
        }

        margin-top: 129px;
        display: flex;
        height: 404px;
        justify-content: space-between;

        .second-hero-title {
            padding-top: 129px;
            width: 40%;
            height: 100%;
            align-self: flex-end;
            display: flex;
            flex-direction: column;
            justify-content: flex-start;
            align-items: flex-start;
            gap: 53px;

            .hero-rect {
                height: 4px;
                width: 50px;
                background-color: #f67e7e;
            }

            h2 {
                color: white;
                font-weight: bold;
                font-size: 48px;
                width: 100%;
            }
        }

        .hero-list {
            padding-top: 200px;
            padding-bottom: 140px;
            width: 540px;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            align-items: flex-start;

            .hero-preview {
                display: flex;
                gap: 25px;
                align-items: flex-start;
                margin-bottom: 30px;

                img {
                    height: 72px;
                    width: 72px;
                    object-fit: contain;
                }

                .hero-preview-txt {
                    display: flex;
                    flex-direction: column;
                    align-items: flex-start;
                    justify-content: space-between;
                    height: 94px;

                    h3 {
                        margin: 0 0;
                        font-weight: bold;
                        font-size: 18px;
                        color: #f67e7e;
                        line-height: 28px;
                        letter-spacing: 0.3px;
                    }
                    p {
                        margin: 0 0;
                        font-weight: 500;
                        font-size: 15px;
                        color: white;
                        line-height: 25px;
                    }
                }
            }
        }
    }

    .third-hero {
        margin-top: 425px;
        display: flex;
        flex-direction: column;

        h1 {
            align-self: center;
            font-weight: bold;
            font-size: 48px;
            line-height: 48px;
            color: white;
            text-align: center;
            width: 85%;

            span {
                color: #79c8c7;
            }
        }

        .quote-list {
            display: flex;
            justify-content: space-between;

            .quote-preview {
                position: relative;
                display: flex;
                flex-direction: column;
                align-content: center;
                width: 350px;
                padding-top: 90px;
                margin-bottom: 150px;

                img.quotes {
                    align-self: center;
                    height: 56px;
                    width: 67px;
                    object-fit: contain;
                    position: absolute;
                    top: 70px;
                    z-index: -1;
                }

                p {
                    font-weight: 500;
                    font-size: 15px;
                    line-height: 25px;
                    text-align: center;
                    color: white;
                }

                h3 {
                    font-weight: bold;
                    font-size: 18px;
                    line-height: 28px;
                    text-align: center;
                    color: #79c8c7;
                }

                h5 {
                    font-weight: 100;
                    font-style: italic;
                    font-size: 13px;
                    line-height: 18px;
                    text-align: center;
                    color: white;
                }

                .avatar {
                    margin-top: 30px;
                    height: 62px;
                    width: 62px;
                    object-fit: contain;
                    border-radius: 50%;
                    align-self: center;
                    box-shadow: 0 0 0 3px white, 0 3px 4px 3px #0000006b;
                }
            }
        }
    }

    .bottom-hero {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: flex-start;

        .background-div {
            position: absolute;
            left: 0;
            width: 100%;
            height: 200px;
            background-color: #f67e7e;
            z-index: -2;
        }

        h2 {
            color: #012f34;
            font-weight: bold;
            font-size: 48px;
            line-height: 48px;
            margin: 0px 0px 0px 100px;
            padding: 0 0;
        }
    }
}
